@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
  
/* base styles */
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  background-image: url(/Yu-Gi-Memoryoto/static/media/castle.3ea5f81f.jpg);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
  color: #fff;
}

html {
  min-height: 100vh;
}

.App {
  max-width: 860px;
  margin: 40px auto;
}

body{
  cursor: url(/Yu-Gi-Memoryoto/static/media/cursor.8f5c9d6e.png), auto;
}
button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 10px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;

}
button:hover {
  background: #c23866;
  color: #fff;
}

.card-grid {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}

.modal-restart{
  background-color: #c23866;
  color: white;
  border-radius: 10px;
}
.modal-restart:hover{
  background-color: #871f41;

}

.modal{
  width:60vw;
  background: #fff;
  height: 60vh;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  color: #c23866;
  border-radius: 15px;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);}
.card {
    position: relative;
    cursor: pointer;

}

.card img {
    width: 100%;
    display: block;
    /* border: 1px solid #fff; */
    box-shadow: 4px 4px 0.4rem rgba(0,0,0,0.8);
    border-radius: 6px
}

.card .front {
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}
.flipped .front{
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
    transition-delay:  0.2s;
}

.card .back {
    transition: all ease-in 0.3s;
    transition-delay: 0.3s
}

.flipped .back {
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    transition-delay: 0s;
}
